/** @format */

@mixin icon($image, $color) {
  background-color: #fff;
  background-image: url($image);
  background-position: center;
  background-repeat: no-repeat;
  border-color: $color;
  box-shadow: inset 0px 5px 6px grey, 0px 8px darken($color, 15%);
}

@mixin bigShadow($color) {
  box-shadow: inset 0px 5px 6px grey, 0px 8px darken($color, 15%),
    0px 0px 0px 50px rgba(#ffffff, 0.07), 0px 0px 0px 100px rgba(#ffffff, 0.05),
    0px 0px 0px 150px rgba(#ffffff, 0.025);
}
@mixin smallShadow($color) {
  box-shadow: inset 0px 5px 6px grey, 0px 8px darken($color, 15%),
    0px 0px 0px 20px rgba(#ffffff, 0.07), 0px 0px 0px 40px rgba(#ffffff, 0.05),
    0px 0px 0px 60px rgba(#ffffff, 0.025);
}
